.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  /* position: absolute; */
  font-family: 'Gantari', 'sans-serif';
  color: #7e7e7e;
}

.App h1 {
  font-size: 36px;
}

.container-home {
  /* position: relative; */
  width: 98%;
  /* background: rgba(255,255,255, 0.9); */
  background: linear-gradient(90deg, rgb(255 224 255) 0%, rgb(208 246 255) 100%);
  margin: 0 auto;
  border-radius: 10px;
  padding: 20px;
}
